// This is a small lib For animations:

@use "../globals" as *;
@use "../helpers" as *;

@mixin pulse {
  &:before,
  &::after {
    content: "";
    position: absolute;
    width: 50px;
    height: 50px;
    z-index: 1;
    cursor: pointer;
    border-radius: $circle;
    animation: pulse 3s infinite;
    background-color: var(--white-light-clr);
  }
  &::after {
    animation-delay: 0.4s;
  }
}
